.section{font-family:"JetBrains Mono",Arial,Helvetica,sans-serif;padding:20px;color:#fff}.section .list{list-style-type:square;padding:0 0 0 20px;margin:30px 0 0}.section .item:not(:last-child){margin-bottom:15px}.section .link{color:inherit;font-size:20px;text-decoration:none;transition:opacity .7s ease;display:flex;flex-direction:row;align-items:center}.section .link:hover{opacity:.6}.section .link.ready::after,.section .link.notready::after{font-size:25px;margin-left:10px}.section .link.ready::after{content:"✔";color:green}.section .link.notready::after{content:"✘";color:red}