@import '../../styles/vars.scss';

.link {
  padding: 5px;
  border: 1px solid $light-theme;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  color: inherit;
  text-decoration: none;
  transition: $transition;
  cursor: pointer;

  &:hover {
    opacity: $opacity;
  }
}
